@import '_variables';
$color1: #f4f4f4;
$color2: #3197EE;

.post__property{
  position: relative;
  clear: both;
  display: block;
  width: 100%;
  float: left;
  margin-top: 0;
  padding: 60px 0;
  min-height: 100vh;
  background-color: #f8f8f8;
  .post__forms{
    overflow: hidden;
    padding: 70px;
    background-color: white;
    margin: 0 auto;
    display: block;
    border-radius: 7px;
    outline: 1px solid #d4d4d4;
    outline-offset: -37px;
  }
  input {height: 50px;}
  h1{margin-bottom: 37px;font-size: 27px;}
  h4{
    margin-bottom: 27px;
    font-size: 18px;
    font-weight: 500;
    border-left: 3px solid #f44336;
    padding-left: 4px;}
  .select__single{height: 50px;
    border-radius: 4px;
    background-color: $white;}
  .mr__27{margin-bottom: 27px;}
.post__radios {
  li{display: inline;float: left;}
  li:first-child {
      margin-left: 0;
  }
  label{margin-right: 17px;}
  
  input[type="radio"] {
    position: absolute;
    opacity: 0;
    + .radio-label {
      &:before {
        content: '';
        background: $color1;
        border-radius: 100%;
        border: 1px solid darken($color1, 25%);
        display: inline-block;
        width: 1.4em;
        height: 1.4em;
        position: relative;
        top: 0px;
        margin-right: 5px; 
        vertical-align: top;
        cursor: pointer;
        text-align: center;
        transition: all 250ms ease;
      }
    }
    &:checked {
      + .radio-label {
        &:before {
          background-color: $color2;
          box-shadow: inset 0 0 0 4px $color1;
        }
      }
    }
    &:focus {
      + .radio-label {
        &:before {
          outline: none;
          border-color: $color2;
        }
      }
    }
   
  }
}
}
