.PG__two{
  background-color: #ffffff;
  padding: 27px;
  float: left;
  width: 100%;
  border: 1px solid #ff4758;
  border-radius: 7px;
  margin-bottom: 37px;
  .lllqqqsss {
    display: inline-block;
    float: left;
    margin: 0 17px 0 0!important;
    padding: 0!important;
  }
  ul {
    list-style-type: none;
  }
  
  li {
    display: inline-block;
    label{
      text-align: center;
      color: black;
      font-weight: 700;
    }
  }
  
  input[type="checkbox"][id^="myCheckbox"] {
    display: none;
  }
  
  label {
    border: 1px solid #fff;
    padding: 10px;
    display: block;
    position: relative;
    margin: 10px;
    cursor: pointer;
   
  }
  
  label:before {
    background-color: white;
    color: white;
    content: " ";
    display: block;
    border-radius: 50%;
    border: 1px solid grey;
    position: absolute;
    top: -5px;
    left: -5px;
    width: 25px;
    height: 25px;
    text-align: center;
    line-height: 20px;
    transition-duration: 0.4s;
    transform: scale(0);
  }
  
  label img {
    height: 60px;
    width: 60px;
    transition-duration: 0.2s;
    transform-origin: 50% 50%;
  }
  
  :checked + label {
    border-color: #f44336;
  }
  
  :checked + label:before {
    content: "✓";
    background-color: #f44336;
    transform: scale(1);
  }
  
  :checked + label img {
    transform: scale(0.9);
    /* box-shadow: 0 0 5px #333; */
    z-index: -1;
  }
}
